
























var userLoginInfo;
export default {
	data() {
		return {
			type: 0,
			personMessage:[],
			personInfo:[]
		}
	},
	onLoad() {
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		// this.version = plus.runtime.version;
		this.personInfo = userLoginInfo;
		this.getPersonal();
	},
	methods: {
		// http://pds.jyt123.com/wxtest/logo.png
		// 保存图片到相册
		// 登录获取用户信息
		getPersonal() {
			uni.request({
				url: this.apiUrl + 'user/getUser',
				method: 'GET',
				data: {
					uid: userLoginInfo.id
				},
				success: res => {
					if (res.data.code === 0) {
						this.personMessage = res.data.data;
						console.log(this.personMessage);
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		save() {
			let path = this.personMessage.qrcode;
			uni.showActionSheet({
				itemList: ['保存图片到相册'],
				success: () => {
					plus.gallery.save(path, function() {
						uni.showToast({
							title: '保存成功',
							icon: 'none'
						})
					}, function() {
						uni.showToast({
							title: '保存失败，请重试！',
							icon: 'none'
						})
					});
				}
			})
		},
	}
}
